.video-background iframe,
.video-foreground {
  pointer-events: none;
}

.video-background iframe,
.video-foreground {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

.video-background {
  background: #000;
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: -99;
}

/* TODO: The responsivity breaks on extremes, like */
/* ultra wide and minimum height or ultra thin and minimum width */
/* also fullscreen is broken */
@media (min-aspect-ratio: 16/9) {
  .video-foreground {
    height: 300%;
    top: -100%;
  }
}
@media (max-aspect-ratio: 16/9) {
  .video-foreground {
    width: 300%;
    left: -100%;
  }
}
@media (max-width: 600px) {
  .video-foreground {
    width: 350%;
    left: -125%;
  }
}
@media (max-height: 400px) {
  .video-foreground {
    height: 500%;
    top: -200%;
  }
}
.audio {
  display: none;
}

